import React, { useEffect, useState } from 'react';
import bemCssModules from 'bem-css-modules';

import PricesStyles from './Prices.module.sass';

const style = bemCssModules(PricesStyles);

const SERICES_DATA = {
    "typ usugi 1": [{ serviceName: 'Usuga 1', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 2', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 3', servicePrice: 'XYZ z' }],
    "typ usugi 2": [{ serviceName: 'Usuga 1', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 2', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 3', servicePrice: 'XYZ z' }],
    "typ usugi 3": [{ serviceName: 'Usuga 1', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 2', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 3', servicePrice: 'XYZ z' }],
    // "typ usugi 4": [{ serviceName: 'Usuga 1', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 2', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 3', servicePrice: 'XYZ z' }],
    // "typ usugi 5": [{ serviceName: 'Usuga 1', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 2', servicePrice: 'XYZ z' }, { serviceName: 'Usuga 3', servicePrice: 'XYZ z' }]
};

export const Prices = () => {

    const [servicesList, setServicesListe] = useState([])

    useEffect(() => {
        //data will be fetched from database thus mapped in useEffect
        const services = Object.keys(SERICES_DATA).map((group, idx) => (
            <div className={style('group')} key={idx}>
                <h3 className={style('type')}>{group}</h3>
                <ul className={style('items')}>
                    {SERICES_DATA[group].map(({ serviceName, servicePrice }, idx) => (<li className={style('item')} key={idx}><div className={style('service')}>{serviceName}</div><div className={style('separator')}></div><div className={style('price')}>{servicePrice}</div></li>))}
                </ul>
            </div>)
        );
        setServicesListe(services);
    }, []);

    return (
        <div className={style()}>
            <div className={style('title')}>
                <h2 className={style('title_text')}>usugi</h2>
            </div>
            <div className={style('services')}>
                {servicesList}
            </div>
        </div>
    );
};